.feedbackCategories {
  display: flex;
  list-style: none;
  padding: 0;
}

.feedbackCategoriesItem:not(:last-child) {
  margin-right: 30px;
}

.feedbackBtn {
  font-size: 18px;
  width: 100px;
  height: 25px;
  border-radius: 5px;
  border: none;
  box-shadow: 0px 1px 3px 2px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
}

.feedbackBtn:hover,
.feedbackBtn:focus {
  background-color: #d9d9d9;
  box-shadow: 0px 1px 1px 1px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 1px rgba(0, 0, 0, 0.2);
}
